import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Categories } from '../category/categories';
import { Api } from '../api/api';
import { Database } from '../database/database.service';
import { Question } from '../question/question';
import { Category } from '../category/category';
import { Answer } from '../answer/answer';

/**
 * Api is a generic REST Api handler. Set your API url first.
 */
@Injectable()
export class Game {

  private _questions = new Array();

  private questionsFromDB = new Array();

  private current = 0;

  constructor(public categories: Categories, public database: Database) {
    this.categories.loadCategories();
  }

  public initQuestions(category: Category): void {
    if (category == null) {
      console.log("Tout choosen");
      this.questionsFromDB = this.database.jtp.chain().find({
        '$and': [{
          'state': {
            '$eq': true // Jamais joué
          }
        }]
      }).data();
    } else {
      console.log(category.label + " choosen");
      this.questionsFromDB = this.database.jtp.chain().find({
        '$and': [{
          'state': {
            '$eq': true // Jamais joué
          },
          'category.id': {
            '$eq': category.id
          }
        }]
      }).data();
    }
    this.randomize(this.questionsFromDB);
  }

  randomize(a) {
    let b, c, d;
    c = a.length;
    while (c) {
      b = Math.random() * c-- | 0;
      d = a[c];
      a[c] = a[b];
      a[b] = d;
    }
  }

  loadQuestions(count: number, replaceAll: boolean = false): void {
    if (replaceAll) {
      console.log("On vide les questions car nouvelle catégorie choisie");
      this.current = 0;
      this.questions = [];
    }
    let questionToInsert = this.questionsFromDB.slice(this.current, this.current + count);
    questionToInsert.forEach(element => {
      this.questions.push(element);
    });
    this.current += count;
  }

  public get questions(): Array<Question> {
    return this._questions;
  }

  public set questions(questions: Array<Question>) {
    this._questions = questions;
  }
}
