import { Injectable } from '@angular/core';

import { Api } from '../api/api';
import { Question } from '../question/question';
import { Answer } from '../answer/answer';
import { Categories } from '../category/categories';

import * as Loki from 'lokijs';
import * as LokiIndexedAdapter from 'lokijs/src/loki-indexed-adapter'

@Injectable({
  providedIn: 'root'
})
export class Database {

  db: Loki;
  jtp: Loki.Collection;

  databaseName: string = 'JTP';

  id_max: number = 0;

  lang: string = 'fr';

  asynTaskRunning: boolean = false;

  constructor(public api: Api, public categories: Categories) { }

  calc = item => {
    let q = new Question();
    q.id = item['i'];
    let countTotal = item['a'] + item['b'];
    q.author = item['p'];
    q.answer1 = new Answer(1, item['r'], item['a'], (item['a'] * 100 / countTotal));
    q.answer2 = new Answer(2, item['s'], item['b'], 100 - q.answer1.percent);
    q.countAnswer = q.answer1.count + q.answer2.count;
    q.goodAnswer = (item['a'] > item['b'] ? q.answer1 : q.answer2);
    q.nbLike = item['l'];
    q.nbDislike = item['u'];
    q.category = this.categories.getById(item['c']);
    return q;
  };

  loadDB() {
    let promise = new Promise((resolve, reject) => {
      let adapter = new LokiIndexedAdapter();
      this.db = new Loki(this.databaseName + '.db', {
        autosave: true,
        //autoload: true,
        autosaveInterval: 4 * 1000,
        adapter: adapter
      });

      this.db.loadDatabase({}, (err) => {
        if (err) {
          console.log("error : " + err);
          return false;
        }
        else {
          this.jtp = this.db.getCollection(this.databaseName);
          resolve();
        }
      });
    });
    return promise;
  }

  initDB(lang: any) {
    let promise = new Promise((resolve, reject) => {
      let param = [];
      param['lang'] = lang;
      this.api.get('initGame', param).subscribe((res: any) => {
        let jtp = this.db.getCollection(this.databaseName);
        if (jtp) {
          this.db.removeCollection(this.databaseName);
        }
        this.jtp = this.db.addCollection(this.databaseName, { 'unique': ['id'] });
        this.jtp.insert(res.items.map(this.calc));

        resolve(true);
      }, err => {
        console.error('ERROR', err);
      });
    });
    return promise;
  }

  startAsynTasks(forceReset = false) {
    if (!this.asynTaskRunning || forceReset) {
      console.log('Démarrage startAsynTasks');

      this.getMoreQ();
      this.getStat();
    }
    this.asynTaskRunning = true;
  }

  getMoreQ() {
    setTimeout(() => {
      console.log('--> getMoreQ');
      let nb = 0;
      let param = [];
      param['lang'] = this.lang;
      param['id_max'] = this.id_max;
      let seq = this.api.get('moreQ', param).subscribe((res: any) => {
        //premier appel
        if (res.fi !== "") {
          this.id_max = res.fi;
        } else if (res.items.length > 0) {
          for (let i = 0; i < res.items.length; i++) {
            if (!this.jtp.by('id', res.items[i].i)) {
              this.jtp.insert(this.calc(res.items[i]));
              nb++;
            }
          }
          this.id_max = res.items[res.items.length - 1].i;
        }
      }, err => {
        console.error('ERROR', err);
      });
      console.log('<-- getMoreQ (+ ' + nb + ' question(s) => ' + this.jtp.data.length + ')');
      this.getMoreQ();
    }, 25000);
  }

  getStat() {
    //un refresh au bout de 30s de jeu
    setTimeout(() => {
      console.log('--> getStat');
      let nb = 0;
      let param = [];
      param['lang'] = this.lang;
      let seq = this.api.get('stat', param).subscribe((res: any) => {
        if (res.items.length > 100) {
          //mise à jour de tous les états à 2
          this.jtp.findAndUpdate({}, function(obj) { });
          for (let i = 0; i < res.items.length; i++) {
            let item = this.jtp.by('id', res.items[i].i);
            if (item) {
              item.answer1.count = res.items[i].a;
              item.answer2.count = res.items[i].b;
              item.nbLike = res.items[i].l;
              item.nbDislike = res.items[i].u;
              this.jtp.update(item);
              nb++;
            }
          }
          // surrpimer tous les état à 2
          //this.jtp.findAndRemove({ 'e': 2 });
        }
      }, err => {
        console.error('ERROR', err);
      });
      console.log('<-- getStat (+ ' + nb + ' question(s) modifiés)');
    }, 35000);
  }
}
