import { Injectable } from '@angular/core';
import { Category } from './category';
import { Api } from '../api/api';

@Injectable()
export class Categories {

  private _categories: Category[];

  private _ids: number[];

  constructor(public api: Api) {
  }

  public loadCategories(): void {
    this._categories = new Array();
    this._ids = new Array();
    this.api.get('get-categories', {}).subscribe(
      (result) => {
        result['items'].forEach((category, index) => {
          let newCategory = new Category(category['id'], category['name'], category['key']);
          this._categories.push(newCategory);
          this._ids[category['id']] = index;
        });
      },
      (err) => {

      },
      () => {
        //this.goToHome();
      });
  }

  public getById(id: number): Category {
    return this._categories[this._ids[id]];
  }

  public get categories(): Category[] {
    return this._categories;
  }
  public set categories(value: Category[]) {
    this._categories = value;
  }

}
