import { Injectable } from '@angular/core';
import { Answer } from '../answer/answer';
import { Category } from '../category/category';

@Injectable()
export class Question {

  private _id: number;

  private _author: string;

  private _answer1: Answer;

  private _answer2: Answer;

  private _countAnswer: number;

  private _goodAnswer: Answer;

  private _category: Category;

  private _nbLike: number;

  private _nbDislike: number;

  /**
   * state = false : déjà joué
   * state = true : pas encore joué
   */
  private _state: boolean;

  private _hide: boolean;

  private _voted: boolean;

  constructor() {
    this._state = true;
    this._hide = false;
    this._hide = false;
  }

  public get id(): number {
    return this._id;
  }
  public set id(value: number) {
    this._id = value;
  }

  public get author(): string {
    return this._author;
  }
  public set author(value: string) {
    this._author = value;
  }

  public get answer1(): Answer {
    return this._answer1;
  }
  public set answer1(value: Answer) {
    this._answer1 = value;
  }

  public get answer2(): Answer {
    return this._answer2;
  }
  public set answer2(value: Answer) {
    this._answer2 = value;
  }

  public get countAnswer(): number {
    return this._countAnswer;
  }
  public set countAnswer(value: number) {
    this._countAnswer = value;
  }

  public get goodAnswer(): Answer {
    return this._goodAnswer;
  }
  public set goodAnswer(value: Answer) {
    this._goodAnswer = value;
  }

  public get state(): boolean {
    return this._state;
  }
  public set state(value: boolean) {
    this._state = value;
  }

  public get hide(): boolean {
    return this._hide;
  }
  public set hide(value: boolean) {
    this._hide = value;
  }

  public get voted(): boolean {
    return this._voted;
  }
  public set voted(value: boolean) {
    this._voted = value;
  }

  public get category(): Category {
    return this._category;
  }
  public set category(value: Category) {
    this._category = value;
  }

  public get nbLike(): number {
    return this._nbLike;
  }
  public set nbLike(value: number) {
    this._nbLike = value;
  }

  public get nbDislike(): number {
    return this._nbDislike;
  }
  public set nbDislike(value: number) {
    this._nbDislike = value;
  }
}
